.at-tag {
  display: inline-block;
  padding: 0 0.68267rem;
  height: 1.28rem;
  color: #666;
  font-size: 0.59733rem;
  text-align: center;
  line-height: -webkit-calc(1.28rem - 2PX);
  line-height: -webkit-calc(60px - 2PX);
  line-height: calc(1.28rem - 2PX);
  vertical-align: middle;
  border: 1PX solid #F7F7F7;
  border-radius: 0.17067rem;
  background-color: #F7F7F7;
  box-sizing: border-box; }
  .at-tag--active {
    color: #78A4F4;
    border: 1PX solid #78A4F4;
    background-color: #FFF; }
    .at-tag--active.at-tag--primary {
      color: #FFF;
      border: 1PX solid #78A4F4;
      background-color: #78A4F4; }
  .at-tag--disabled {
    opacity: 0.3; }
  .at-tag--small {
    padding: 0 0.34133rem;
    height: 0.85333rem;
    font-size: 0.42667rem;
    line-height: -webkit-calc(0.85333rem - 2PX);
    line-height: -webkit-calc(40px - 2PX);
    line-height: calc(0.85333rem - 2PX); }
  .at-tag--circle {
    border-radius: 0.64rem;
    background-clip: border-box;
    overflow: hidden; }
